<template>
  <div id="app">
    <header>
      <img
        src="https://cdn.prod.website-files.com/65363d385688281ce5b016ec/65363d385688281ce5b016f5_Vectors-Wrapper.svg"
        alt="Celfocus Logo"
      />
    </header>
    <h1>Camera Test</h1>
    <!-- Toggle for camera mode. Disabled if scanner is running -->
    <div class="toggle-mode">
      <label>
        <input
          type="radio"
          v-model="cameraMode"
          value="inline"
          :disabled="isScannerRunning"
        />
        Inline
      </label>
      <label>
        <input
          type="radio"
          v-model="cameraMode"
          value="modal"
          :disabled="isScannerRunning"
        />
        Modal
      </label>
    </div>

    <!-- Inline Mode: Render the camera container on the page -->
    <div
      v-if="cameraMode === 'inline'"
      id="camera-container"
      @click="toggleScanner"
      :key="scannerKey"
    >
      <div id="scanner" ref="scannerContainer"></div>
    </div>

    <!-- Modal Mode: Render a modal overlay when scanner is active -->
    <div
      v-if="cameraMode === 'modal' && showModal"
      class="modal-overlay"
      @click.self="stopScanner"
    >
      <div class="modal-content" :key="scannerKey">
        <div id="scanner" ref="scannerContainer"></div>
        <button class="close-btn" @click="stopScanner">Close</button>
      </div>
    </div>

    <div id="output">
      Scanned Result: <span>{{ scannedResult }}</span>
    </div>
    <!-- Display error message if any -->
    <div v-if="errorMessage" class="error-message">
      {{ errorMessage }}
    </div>
    <!-- Buttons to manually start/stop the scanner -->
    <button @click="startScanner" :disabled="isScannerRunning">
      Start Scanner
    </button>
    <button @click="stopScanner" :disabled="!isScannerRunning">
      Stop Scanner
    </button>
  </div>
</template>

<script>
import Quagga from 'quagga';

export default {
  name: 'CameraScanner',
  data() {
    return {
      isScannerRunning: false,
      scannedResult: 'None',
      cameraStream: null,
      errorMessage: '',
      errorTimer: null,
      // Camera mode can be 'inline' or 'modal'
      cameraMode: 'inline',
      // For modal mode: controls whether the modal is shown
      showModal: false,
      // Dynamic key to force re-render of the scanner container
      scannerKey: 0
    };
  },
  watch: {
    // When the camera mode changes, increment the key so that the container is re-rendered
    cameraMode() {
      this.scannerKey++;
    }
  },
  methods: {
    // Helper to show an error message for 5 seconds
    showError(message) {
      if (this.errorTimer) {
        clearTimeout(this.errorTimer);
      }
      this.errorMessage = message;
      this.errorTimer = setTimeout(() => {
        this.errorMessage = '';
        this.errorTimer = null;
      }, 5000);
    },
    // Common logic to initialize and start the scanner
    initScanner() {
      // Clear previous errors
      if (this.errorTimer) {
        clearTimeout(this.errorTimer);
        this.errorTimer = null;
      }
      this.errorMessage = '';

      Quagga.init(
        {
          inputStream: {
            type: 'LiveStream',
            target: this.$refs.scannerContainer,
            constraints: {
              facingMode: 'environment'
            }
          },
          decoder: {
            readers: [
              'code_128_reader',
              'ean_reader',
              'ean_8_reader',
              'code_39_reader'
            ]
          }
        },
        (err) => {
          if (err) {
            console.error('QuaggaJS Initialization Error:', err);
            if (
              err.name === 'NotAllowedError' ||
              err.name === 'PermissionDeniedError'
            ) {
              this.showError(
                'Camera access was denied. Please allow camera permissions.'
              );
            } else if (
              err.name === 'NotReadableError' ||
              err.name === 'TrackStartError'
            ) {
              this.showError(
                'The camera is currently in use by another application. Please close it and try again.'
              );
            } else {
              this.showError('Unable to access the camera. Please try again later.');
            }
            if (this.cameraMode === 'modal') {
              this.showModal = false;
            }
            return;
          }
          Quagga.start();
          this.isScannerRunning = true;
          console.log('QuaggaJS started');

          const video = this.$refs.scannerContainer.querySelector('video');
          this.cameraStream = video ? video.srcObject : null;
        }
      );

      Quagga.onDetected((data) => {
        this.scannedResult = data.codeResult.code;
        console.log('Detected code:', data.codeResult.code);
      });
    },
    startScanner() {
      if (this.isScannerRunning) return;

      // Force a fresh render of the scanner container
      this.scannerKey++;
      // Always wait for the DOM to update before initializing
      this.$nextTick(() => {
        if (this.cameraMode === 'modal') {
          this.showModal = true;
          this.$nextTick(() => {
            this.initScanner();
          });
        } else {
          this.initScanner();
        }
      });
    },
    stopScanner() {
      if (!this.isScannerRunning) return;

      Quagga.stop();

      // Explicitly reset the video element (if it exists)
      const scannerContainer = this.$refs.scannerContainer;
      if (scannerContainer) {
        const video = scannerContainer.querySelector('video');
        if (video) {
          video.pause();
          video.srcObject = null;
        }
      }
      // Force re-render of the scanner container
      this.scannerKey++;

      if (this.cameraMode === 'modal') {
        this.showModal = false;
      } else if (this.cameraMode === 'inline') {
        // Optionally clear the container
        this.$refs.scannerContainer.innerHTML = '';
      }
      if (this.cameraStream) {
        this.cameraStream.getTracks().forEach((track) => track.stop());
        this.cameraStream = null;
      }
      Quagga.offDetected();
      this.isScannerRunning = false;
      console.log('QuaggaJS stopped');
      if (this.errorTimer) {
        clearTimeout(this.errorTimer);
        this.errorTimer = null;
      }
      this.errorMessage = '';
    },
    // Toggle scanner when clicking on the camera container (only for inline mode)
    toggleScanner() {
      if (this.cameraMode === 'inline') {
        this.isScannerRunning ? this.stopScanner() : this.startScanner();
      }
    }
  },
  beforeUnmount() {
    if (this.isScannerRunning) {
      this.stopScanner();
    }
    if (this.errorTimer) {
      clearTimeout(this.errorTimer);
      this.errorTimer = null;
    }
  }
};
</script>

<style scoped>
/* General Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  text-align: center;
  background-color: #ffffff;
  color: #333333;
}

header {
  margin: 20px 0;
}

header img {
  width: 150px;
  height: auto;
}

h1 {
  font-size: 36px;
  margin: 20px 0;
  color: #1e1e1e;
  font-weight: bold;
}

/* Toggle controls */
.toggle-mode {
  margin-bottom: 20px;
}
.toggle-mode label {
  margin-right: 15px;
  font-weight: bold;
}

/* Inline mode container */
#camera-container {
  position: relative;
  width: 100%;
  max-width: 600px;
  height: 350px;
  margin: 20px auto;
  border: 1px solid #e0e0e0;
  background-color: #f4f4f4;
  overflow: hidden;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}

/* Modal styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}
.modal-content {
  position: relative;
  width: 600px;
  max-width: 90%;
  height: 350px;
  background: #f4f4f4;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}
.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: #1e1e1e;
  color: white;
  border: none;
  border-radius: 50px;
  padding: 5px 10px;
  cursor: pointer;
}

/* Scanner video styling */
#scanner video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-color: #000;
}

#output {
  margin-top: 20px;
  font-size: 1.2em;
  color: #1e1e1e;
  font-weight: bold;
}

.error-message {
  color: red;
  margin: 10px 0;
  font-weight: bold;
}

button {
  padding: 10px 20px;
  margin: 15px 10px;
  font-size: 1em;
  font-weight: bold;
  color: #ffffff;
  background-color: #1e1e1e;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
}

button:hover {
  background-color: #ff5a5f;
}

button:disabled {
  background-color: #b3b3b3;
  cursor: not-allowed;
}
</style>
